import {Component} from '@angular/core';
import {Modal} from "../../../shared/service/modal/modal.model";
import {InputModalDto} from "../../../shared/service/modal/modal.service";
import {UploadSingleMediaGCSDto} from "../../../shared/service/upload-file/upload-file.dto";
import {MediaFolderTypes} from "@youtap-web-lib";
import {UploadFileService} from "../../../shared/service/upload-file/upload-file.service";
import {environment} from "../../../../environments/environment";

@Component({
  selector: 'confirm-upload-modal',
  templateUrl: './confirm-upload-modal.component.html'
})
export class ConfirmUploadModalComponent extends Modal {
  title!: string;
  showButton = false;
  imageSrc!: string | ArrayBuffer | null;
  file!: File;
  type!: string;

  constructor(
    private uploadFileService: UploadFileService,
  ) {
    super();
  }

  onInjectInputs(inputs: InputModalDto): void {
    this.showButton = inputs.closeButton;
    this.imageSrc = inputs.data.imageSrc;
    this.file = inputs.data.file;
    this.type = inputs.data.type;
  }

  save(value: string): void {
    this.close(value);
  }

  cancel(): void {
    this.dismiss({});
  }

  saveToGcs() {
    const data: UploadSingleMediaGCSDto = {
      createSingleMediaInput: {
        file: this.file,
        folder: this.type === 'ktp' ? MediaFolderTypes.ID_CARD
          : this.type === 'selfie' ? MediaFolderTypes.SELFIE_WITH_ID
            : this.type === 'signature' ? MediaFolderTypes.SIGNATURE
              : MediaFolderTypes.MERCHANT_REGISTRATION
      }
    };

    this.uploadFileService.uploadSingleMediaRegistrationGCS(environment.BOS_API_GRAPHQL_URL, data)
      .then((data) => {
        this.close(data);
      }).catch(() => {
        this.cancel();
    });
  }

  retryUploads() {
    this.close('retry');
  }
}
