import { Component, EventEmitter, Input, Output } from '@angular/core';
import {
  DetailLocation,
} from '@youtap-web-lib';
import { environment } from '../../../../environments/environment';

@Component({
  selector: 'pick-location',
  templateUrl: './pick-location.component.html'
})
export class PickLocationComponent {
  apiKey = environment.GOOGLE_MAPS_API_KEY;
  latlng!: google.maps.LatLngLiteral;
  @Input() addressDetail = '';
  @Input() type = 'page';
  @Input() position!: { lat: number; lng: number };
  // eslint-disable-next-line @typescript-eslint/no-explicit-any
  @Output() selectLocation = new EventEmitter<any>();

  detailPosition(data: DetailLocation) {
    this.addressDetail = data.addressDetail;
    this.selectLocation.emit(data);
  }
}
