import {
  Component,
  EventEmitter,
  forwardRef,
  Input,
  Output
} from '@angular/core';
import {
  ListPostalCodeDto,
  PostalCodeService,
  PostalCode
} from '@youtap-web-lib';
import { NG_VALUE_ACCESSOR } from '@angular/forms';
import { data } from 'autoprefixer';

@Component({
  selector: 'postal-dropdown',
  templateUrl: './postal-dropdown.component.html',
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: forwardRef(() => PostalDropdownComponent),
      multi: true
    }
  ]
})
export class PostalDropdownComponent {
  value: PostalCode = { id: '', code: '' };
  id = '';
  dataPostalCode: PostalCode[] = [];
  @Input() placeHolder = 'Pilih Kode Pos';
  @Input() set villageIds(data: string[]) {
    if (data.length) {
      this.value = {id: '', name: ''};
      this.dataPostalCode = [];
      this.villageId = data;
      this.getListPostalCode(data);
    }
  }
  @Output() valueChange: EventEmitter<string> = new EventEmitter<string>();
  isLoading = false;
  isBusy = false;
  @Input() disabled = false;
  shareModal = false;
  villageId!: string[];

  constructor(private postalCodeService: PostalCodeService) {}

  onChange: (data: string) => void = () => {
    data;
  };
  onTouch: (data: string) => void = () => {
    data;
  };

  changeDrop(data: PostalCode) {
    this.onChange(data.id);
    this.onTouch(data.id);
    this.value = data;
    this.valueChange.emit(data.id);
    this.shareModal = false;
  }

  // this method sets the value programmatically
  writeValue(value: string) {
    this.id = value;
  }

  // upon UI element value changes, this method gets triggered
  registerOnChange(fn: (data: string) => void) {
    this.onChange = fn;
  }

  // upon touching the element, this method gets triggered
  registerOnTouched(fn: (data: string) => void) {
    this.onTouch = fn;
  }

  getListPostalCode(id: string[]) {
    this.isBusy = true;
    const data: ListPostalCodeDto = {
      listPostalCodeInput: {
        limit: 50,
        page: 0,
        filter: {
          villageIds: id
        }
      }
    };
    this.postalCodeService.listPostalCode(data).subscribe({
      next: ({ data }) => {
        this.dataPostalCode = data.listPostalCode.postalCodes;
        data.listPostalCode.postalCodes.map((val: any) => {
          if (val.id === this.id) {
            this.value = val;
          }
        });
        this.isBusy = false;
      },
      error: (err: any) => {
        err;
        this.isLoading = false;
      }
    });
  }

  openShareModal() {
    if (!this.shareModal) {
      this.shareModal = true;
    }
  }
}
