import { Component } from '@angular/core';
import {Modal} from "../../../shared/service/modal/modal.model";
import {InputModalDto} from "../../../shared/service/modal/modal.service";

@Component({
  selector: 'tnc-verification',
  templateUrl: './tnc-verification.component.html'
})
export class TncVerificationComponent extends Modal{
  title!: string;
  showButton = false;
  isCheck!: boolean

  onInjectInputs(inputs: InputModalDto): void {
    this.title = inputs.title;
    this.showButton = inputs.closeButton;
  }

  save(value: boolean): void {
    this.close(value);
  }

  cancel(): void {
    this.dismiss({});
  }

  handleChangeDisabledButton() {
    this.isCheck = !this.isCheck
  }
}
