import {
  Component,
  EventEmitter,
  forwardRef,
  Input,
  OnInit,
  Output
} from '@angular/core';
import { NG_VALUE_ACCESSOR } from '@angular/forms';
import { data } from 'autoprefixer';
import {District, ListVillageDto, Village, VillageService} from "@youtap-web-lib";

@Component({
  selector: 'village-dropdown',
  templateUrl: './village-dropdown.component.html',
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: forwardRef(() => VillageDropdownComponent),
      multi: true
    }
  ]
})
export class VillageDropdownComponent {
  dataVillage: Village[] = [];
  value: Village = { id: '', name: '' };
  id = '';
  isBusy = false;
  isReady = false;
  district!: string;
  village!: string;
  postal!: string;
  @Input() disabled = false;
  @Input() placeHolder = 'Pilih Kelurahan';
  @Output() valueChange: EventEmitter<string> = new EventEmitter<string>();

  @Input() set districtId(data: string) {
    if (data) {
      this.search = '';
      this.value = {id: '', name: ''}
      this.dataVillage = [];
      this.district = data;
      this.getListVillage();
    }
  }

  @Input() set villageId(data: string) {
    if (data) {
      this.village = data;
      this.getListVillage();
    }
  }

  @Input() set postalCode(data: string) {
    if (data) {
      this.postal = data;
      this.getListVillage();
    }
  }
  shareModal = false;
  search = '';

  constructor(private villageService: VillageService) {}

  ngAfterViewInit() {
    Promise.resolve().then(() => (this.isReady = true));
  }

  onChange: (data: string) => void = () => {
    data;
  };

  onTouch: (data: string) => void = () => {
    data;
  };

  changeDrop(data: District) {
    this.onChange(data.id);
    this.onTouch(data.id);
    this.value = data;
    this.valueChange.emit(data.id);
    this.shareModal = false;
  }

  // this method sets the value programmatically
  writeValue(value: string) {
    if (value) {
        this.id = value;
    } else {
      this.id = "";
      this.value = {id: '', name: ''};
    }
  }

  // upon UI element value changes, this method gets triggered
  registerOnChange(fn: (data: string) => void) {
    this.onChange = fn;
  }

  // upon touching the element, this method gets triggered
  registerOnTouched(fn: (data: string) => void) {
    this.onTouch = fn;
  }

  getListVillage() {
    this.isBusy = true;
    const data: ListVillageDto = {
      listVillageInput: {
        limit: 50,
        page: 0,
        search: this.search,
        filter: {
          id: this.village,
          districtId: this.district,
          postalCode: this.postal
        }
      }
    };
    this.villageService.listVillage(data).subscribe({
      next: ({ data }) => {
        this.dataVillage = data.listVillage.villages;
        if (this.dataVillage) {
          data.listVillage.villages.map((val) => {
            if (val.id === this.id) {
              this.value = val;
            }
          });
        }
        this.isBusy = false;
      },
      error: (err) => {
        err;
        this.isBusy = false;
      }
    });
  }

  openShareModal() {
    if (!this.shareModal) {
      this.shareModal = true;
      this.search = '';
      this.dataVillage = [];
      this.getListVillage();
    }
  }

  searchVillage(){
    setTimeout(() => {
      this.dataVillage = [];
      this.getListVillage();
    }, 1500);
  }
}
