import { Injectable } from '@angular/core';
import {  CanActivate, Router } from '@angular/router';
import { AuthData } from '../shared/data/auth.data';
import { Observable } from 'rxjs';
import {AccountTypes} from "@youtap-web-lib";

@Injectable({
  providedIn: 'root'
})
export class NoLoginGuard implements CanActivate {
  constructor(private authData: AuthData, private router: Router) {}

  canActivate(): Observable<boolean> | boolean {
    const token = this.authData.token;
    const isVerified = this.authData.user ? this.authData.user.merchant.accountType : AccountTypes.basic;
    if (!token) {
      return true;
    } else {
      if (isVerified == AccountTypes.premium) {
        this.router.navigate(['/']);
        return false;
      }
      return true;
    }
  }
}
