import {
  Component,
  EventEmitter,
  forwardRef,
  Input,
  OnInit,
  Output
} from '@angular/core';
import {ListBankDto, Bank, BankService, DetailBankInputDto, VerificationService} from '@youtap-web-lib';
import { NG_VALUE_ACCESSOR } from '@angular/forms';
import { data } from 'autoprefixer';

@Component({
  selector: 'bank-dropdown',
  templateUrl: './bank-dropdown.component.html',
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: forwardRef(() => BankDropdownComponent),
      multi: true
    }
  ],
  styleUrls: ['./bank-dropdown.component.scss']
})
export class BankDropdownComponent implements OnInit {
  value: Bank = { id: '', name: '' };
  id = '';
  dataBank: Bank[] = [];
  @Input() placeHolder = 'Pilih Bank';
  @Output() valueChange: EventEmitter<string> = new EventEmitter<string>();
  isLoading = true;
  shareModal = false;
  search = '';
  @Input() bankId!: string;

  constructor(
    private bankService: BankService,
    private verificationService: VerificationService,
  ) {}

  ngOnInit(): void {
    if (this.bankId) {
      this.getDetailBank();
    }
    this.getListBank(1);
  }

  openShareModal() {
    if (!this.shareModal) {
      this.shareModal = true;
      this.search = '';
      this.dataBank = [];
      this.getListBank(1);
    }
  }

  onChange: (data: string) => void = () => {
    data;
  };
  onTouch: (data: string) => void = () => {
    data;
  };

  changeDrop(data: Bank) {
    this.value = data;
    this.shareModal = false;
    this.valueChange.emit(data.id);
    this.onChange(data.id);
    this.onTouch(data.id);
  }

  writeValue(value: string) {
    this.id = value;
  }

  registerOnChange(fn: (data: string) => void) {
    this.onChange = fn;
  }

  registerOnTouched(fn: (data: string) => void) {
    this.onTouch = fn;
  }

  getDetailBank() {
    const data: DetailBankInputDto = {
      detailBankInput: {
        bankId: this.bankId,
      },
    };

    this.verificationService.detailBank(data).subscribe({
      next: (data) => {
        if (data.data.name != null) {
          this.value = {
            name: data.data.name,
            id: data.data.id
          };
        }
      }, error: (error) => {
        return error;
      }
    });
  }

  getListBank(page: number) {
    this.isLoading = true;
    const data: ListBankDto = {
      listBankInput: {
        limit: 50,
        page,
        search: this.search
      }
    };
    this.bankService.listBank(data).subscribe({
      next: ({ data }: any) => {
        if (data.listBank && data.listBank.banks) {
          if (this.dataBank.length) {
            this.dataBank = this.dataBank.concat(data.listBank.banks);
          } else {
            this.dataBank = data.listBank.banks;
          }
          this.getListBank(page + 1);

          if (this.id) {
            data.listBank.banks.map((val: any) => {
              if (val.id === this.id) {
                this.value = val;
              }
            });
          }
        }

        this.isLoading = false;
      },
      error: (err: any) => {
        err;
        this.isLoading = false;
      }
    });
  }

  searchBank(){
    setTimeout(() => {
      this.dataBank = [];
      this.getListBank(1);
    }, 1500);
  }

  reset(){
    this.search = ''
    this.getListBank(1)
  }

  closeModal() {
    this.shareModal = false;
  }
}
