import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, ValidatorFn, Validators } from "@angular/forms";
import {
  Address, AddressDetail, BankAccount, CreateAddressDetailInputDto,
  CreatePersonalInformationInputDto, ERROR_MESSAGE, PersonalInformation,
  ReadMediaRegistrationGCSInputDto,
  ResponseDetailMerchantRegistrationUserDto,
  ValidationService,
  VerificationService
} from "@youtap-web-lib";
import { ModalService } from "../../../shared/service/modal/modal.service";
import { UploadPhotoModalComponent } from "../upload-photo-modal/upload-photo-modal.component";
import { ConfirmUploadModalComponent } from "../confirm-upload-modal/confirm-upload-modal.component";
import { TncVerificationComponent } from "../tnc-verification/tnc-verification.component";
import { NavigationExtras, Router } from "@angular/router";
import { PlatformTypeService } from "../../../shared/service/platform-type.service";
import { UploadImageComponent } from "../../../shared/components/upload-image/upload-image.component";
import { UploadImageSelfieComponent } from "../../../shared/components/upload-image-selfie/upload-image-selfie.component";
import { GaService } from "../../../shared/service/ga.service";
import { Location } from "@angular/common";


@Component({
  selector: 'kyc-verification',
  templateUrl: './kyc-verification.component.html',
})
export class KycVerificationComponent implements OnInit {
  showStep1 = '';
  showStep2 = '';
  personalForm!: FormGroup;
  bankForm!: FormGroup;
  addressForm!: FormGroup;
  isCheck = false;
  shouldNameEqual = true;
  stepCheck = false;
  personalInformation!: PersonalInformation;
  bankAccount!: BankAccount;
  addressDetail!: AddressDetail;
  percentage = '50%';
  identityCardImage!: string;
  selfieIdentityCardImage!: string;
  signatureImage!: string;
  onlineStoreChecked!: boolean;
  offlineStoreChecked!: boolean;
  offlineStoreImage!: string;
  showMap = false;
  pickLocationTemp!: Address;
  addressMap = "";
  loadingSubmit = false;
  isMobile = false
  isTablet = false
  invalidReferral = false;
  isDisabledField: boolean = false;

  constructor(
    private formBuilder: FormBuilder,
    private modalService: ModalService,
    private verificationService: VerificationService,
    private router: Router,
    private platformTypeService: PlatformTypeService,
    public gaService: GaService,
    private location: Location
  ) {
    this.personalForm = this.formBuilder.group({
      name: ["", Validators.compose([Validators.required, ValidationService.alphaValidator])],
      identityCardNumber: ["", Validators.compose([
        Validators.required,
        Validators.minLength(16),
        Validators.maxLength(16),
        ValidationService.numValidator]
      )],
      taxIdentityCardNumber: ["", Validators.compose([ValidationService.numValidator, Validators.maxLength(20),])],
      email: ["", Validators.compose([Validators.email])],
      referralCode: [""],
      identityCardImage: ["", Validators.compose([Validators.required])],
      selfieIdentityCardImage: ["", Validators.compose([Validators.required])],
      signatureImage: ["", Validators.compose([Validators.required])],
    });

    this.bankForm = this.formBuilder.group({
      accountNumber: ["", Validators.compose([Validators.required, Validators.maxLength(20), ValidationService.numValidator])],
      bankId: ["", Validators.compose([Validators.required])],
      name: ["", Validators.compose([Validators.required, Validators.maxLength(50), ValidationService.alphaValidator])]
    });

    this.addressForm = this.formBuilder.group({
      name: ["", Validators.compose([Validators.required, Validators.maxLength(225)])],
      onlineStoreUrl: [""],
      offlineStorePhotoUrl: [""],
      provinceId: ["", Validators.compose([Validators.required])],
      regencyId: ["", Validators.compose([Validators.required])],
      districtId: ["", Validators.compose([Validators.required])],
      villageId: ["", Validators.compose([Validators.required])],
      postalCodeId: ["", Validators.compose([Validators.required])],
      description: ["", Validators.compose([Validators.required])],
      latitude: [null, Validators.compose([Validators.required])],
      longitude: [null, Validators.compose([Validators.required])],
    });
  }

  ngOnInit() {
    this.isMobile = this.platformTypeService.checkIsMobile();
    this.isTablet = this.platformTypeService.checkIsTablet();
    this.getDetailUserRegistration();
    this.subscribeToReferralCodeChanges()
  }

  subscribeToReferralCodeChanges() {
    this.personalForm.get('referralCode')?.valueChanges.subscribe(() => {
      this.invalidReferral = false;
    });
  }

  checkStepsCondition() {
    if (this.personalInformation && this.bankAccount) {
      this.showStep1 = 'none';
      this.showStep2 = '';
      this.stepCheck = true;
      this.percentage = '100%';
    } else {
      this.showStep1 = '';
      this.showStep2 = 'none';
    }
  }

  checkDataMerchantRegistration(data: ResponseDetailMerchantRegistrationUserDto) {
    this.checkStepsCondition();

    if (this.personalInformation) {
      this.personalForm.controls['name'].setValue(data.detailMerchantRegistrationUser.personalInformation.name);
      this.personalForm.controls['identityCardNumber'].setValue(data.detailMerchantRegistrationUser.personalInformation.identityCardNumber);
      this.personalForm.controls['taxIdentityCardNumber'].setValue(data.detailMerchantRegistrationUser.personalInformation.taxIdentityCardNumber);
      this.personalForm.controls['email'].setValue(data.detailMerchantRegistrationUser.personalInformation.email);
      this.personalForm.controls['referralCode'].setValue(data.detailMerchantRegistrationUser.personalInformation.referralCode);
      this.personalForm.controls['identityCardImage'].setValue(data.detailMerchantRegistrationUser.personalInformation.identityCardImage);
      this.personalForm.controls['selfieIdentityCardImage'].setValue(data.detailMerchantRegistrationUser.personalInformation.selfieIdentityCardImage);
      this.personalForm.controls['signatureImage'].setValue(data.detailMerchantRegistrationUser.personalInformation.signatureImage);
    } else {
      this.personalForm.controls['referralCode'].setValue(data.detailMerchantRegistrationUser.referralCode);
    }

    this.personalForm.controls['referralCode'].value && this.personalForm.controls['referralCode'].disable()

    if (this.bankAccount) {
      this.bankForm.controls['name'].setValue(data.detailMerchantRegistrationUser.bankAccount.name);
      this.bankForm.controls['bankId'].setValue(data.detailMerchantRegistrationUser.bankAccount.bankId);
      this.bankForm.controls['accountNumber'].setValue(data.detailMerchantRegistrationUser.bankAccount.accountNumber);
    }

    if (this.addressDetail) {
      this.addressForm.controls['name'].setValue(data.detailMerchantRegistrationUser.addressDetail.name);
      this.addressForm.controls['onlineStoreUrl'].setValue(data.detailMerchantRegistrationUser.addressDetail.onlineStoreUrl);
      this.addressForm.controls['offlineStorePhotoUrl'].setValue(data.detailMerchantRegistrationUser.addressDetail.offlineStorePhotoUrl);
      this.addressForm.controls['provinceId'].setValue(data.detailMerchantRegistrationUser.addressDetail.provinceId);
      this.addressForm.controls['regencyId'].setValue(data.detailMerchantRegistrationUser.addressDetail.regencyId);
      this.addressForm.controls['districtId'].setValue(data.detailMerchantRegistrationUser.addressDetail.districtId);
      this.addressForm.controls['villageId'].setValue(data.detailMerchantRegistrationUser.addressDetail.villageId);
      this.addressForm.controls['postalCodeId'].setValue(data.detailMerchantRegistrationUser.addressDetail.postalCodeId);
      this.addressForm.controls['description'].setValue(data.detailMerchantRegistrationUser.addressDetail.description);
      this.addressForm.controls['latitude'].setValue(data.detailMerchantRegistrationUser.addressDetail.latitude);
      this.addressForm.controls['longitude'].setValue(data.detailMerchantRegistrationUser.addressDetail.longitude);
    }
  }

  getDetailUserRegistration() {
    this.verificationService.detailMerchantRegistrationUser().subscribe({
      next: ({ data }) => {
        //check data referral code if have data then true 
        data.detailMerchantRegistrationUser.referralCode ? this.isDisabledField = true : this.isDisabledField = false;

        this.personalInformation = data.detailMerchantRegistrationUser.personalInformation;
        this.bankAccount = data.detailMerchantRegistrationUser.bankAccount;
        this.addressDetail = data.detailMerchantRegistrationUser.addressDetail;
        this.checkDataMerchantRegistration(data);
        this.readDataPhoto();
      }
    })
  }

  stepAction(step: number) {
    if (step === 1) {
      this.showStep1 = '';
      this.showStep2 = 'none';
    } else if (step === 2) {
      this.showStep1 = 'none';
      this.showStep2 = '';
    }
  }


  showButtonUploads(type: string) {
    if (this.isMobile || this.isTablet) {
      const modalCtrl = this.modalService.open(UploadPhotoModalComponent, {
        title: type === 'ktp' ? 'Upload KTP'
          : type === 'selfie' ? 'Foto Dirimu dengan KTP'
            : type === 'store' ? 'Upload Foto Tokomu dari Luar'
              : 'Tanda Tangan',
        closeButton: true,
        data: {
          type: type,
        }
      });
      modalCtrl.onResult().subscribe((data) => {
        if (type === 'signature') {
          this.personalForm.controls['signatureImage'].setValue(data);
          this.readDataPhoto();
        } else {
          this.openConfirmation(data, type)
        }
      })
    } else {
      if (type === 'ktp') {
        this.openKtp()
      } else if (type === 'selfie') {
        this.openSelfie();
      } else if (type === 'store') {
        const modalCtrl = this.modalService.open(UploadPhotoModalComponent, {
          title: 'Upload Foto Tokomu dari Luar',
          closeButton: true,
          data: {
            type: 'store',
          }
        });
        modalCtrl.onResult().subscribe((data) => {
          this.openConfirmation(data, type);
        })
      } else if (type === 'signature') {
        const modalCtrl = this.modalService.open(UploadPhotoModalComponent, {
          title: 'Tanda Tangan',
          closeButton: true,
          data: {
            type: 'signature',
          }
        });
        modalCtrl.onResult().subscribe((data) => {
          this.personalForm.controls['signatureImage'].setValue(data);
          this.readDataPhoto();
          // this.openConfirmation(data, type);
        })
      }
    }
  }

  openKtp() {
    const modalCtrl = this.modalService.open(UploadImageComponent, {
      title: "Upload KTP",
      closeButton: true,
      data: {
        imageSrc: this.identityCardImage
      }
    })
    modalCtrl.onResult().subscribe((data) => {
      if (data) {
        this.personalForm.controls['identityCardImage'].setValue(data)
        this.readDataPhoto()
      }
    })
  }

  openSelfie() {
    const modalCtrl = this.modalService.open(UploadImageSelfieComponent, {
      title: 'Foto Dirimu dengan KTP',
      closeButton: true,
      data: {
        imageSrc: this.selfieIdentityCardImage
      }
    });
    modalCtrl.onResult().subscribe((data) => {
      if (data) {
        this.personalForm.controls['selfieIdentityCardImage'].setValue(data)
        this.readDataPhoto()
      }
    })
  }

  // eslint-disable-next-line @typescript-eslint/no-explicit-any
  openConfirmation(data: any, type: string) {
    const confirmModal = this.modalService.open(
      ConfirmUploadModalComponent,
      {
        title: data.type === 'ktp' ? 'Upload KTP'
          : data.type === 'selfie' ? 'Foto Dirimu dengan KTP'
            : data.type === 'store' ? 'Upload Foto Tokomu dari Luar'
              : 'Tanda Tangan',
        closeButton: true,
        data: {
          imageSrc: data.imageSrc,
          file: data.file,
          type: data.type,
        }
      }
    );

    confirmModal.onResult().subscribe((data) => {
      if (data === 'retry') {
        this.gaService.addEvent('kyc_registration_step_retake_' + type, 'Register_kyc', '')
        this.showButtonUploads(type);
      } else {
        this.gaService.addEvent('kyc_registration_step_upload_success' + type, 'Register_kyc', '')
        if (type === 'ktp') {
          this.personalForm.controls['identityCardImage'].setValue(data.data.uploadSingleMediaRegistrationGCS.url);
          this.readDataPhoto();
        } else if (type === 'selfie') {
          this.personalForm.controls['selfieIdentityCardImage'].setValue(data.data.uploadSingleMediaRegistrationGCS.url);
          this.readDataPhoto();
        } else if (type === 'signature') {
          this.personalForm.controls['signatureImage'].setValue(data.data.uploadSingleMediaRegistrationGCS.url);
          this.readDataPhoto();
        } else if (type === 'store') {
          this.addressForm.controls['offlineStorePhotoUrl'].setValue(data.data.uploadSingleMediaRegistrationGCS.url);
          this.readDataPhoto();
        }
      }
    });
  }

  readDataPhoto() {
    const data: ReadMediaRegistrationGCSInputDto = {
      readMediaInput: {
        expiry: 60,
        urls: [
          this.personalForm.controls['identityCardImage'].value ? this.personalForm.controls['identityCardImage'].value : '',
          this.personalForm.controls['selfieIdentityCardImage'].value ? this.personalForm.controls['selfieIdentityCardImage'].value : '',
          this.personalForm.controls['signatureImage'].value ? this.personalForm.controls['signatureImage'].value : '',
          this.addressForm.controls['offlineStorePhotoUrl'].value ? this.addressForm.controls['offlineStorePhotoUrl'].value : '',
        ],
      },
    };

    this.verificationService.readMediaRegistrationGCS(data).subscribe({
      next: ({ data }) => {
        if (data) {
          this.identityCardImage = data.readMediaRegistrationGCS.urls[0];
          this.selfieIdentityCardImage = data.readMediaRegistrationGCS.urls[1];
          this.signatureImage = data.readMediaRegistrationGCS.urls[2];
          this.offlineStoreImage = data.readMediaRegistrationGCS.urls[3];
        }
      }
    })
  }

  handleOnlineStoreChecked() {
    this.gaService.addEvent('kyc_registration_step_upload_business_type_online', 'Register_otp', '')
    const validators: ValidatorFn[] = [ValidationService.urlValidator];
    this.onlineStoreChecked = !this.onlineStoreChecked
    if (!this.onlineStoreChecked) {
      this.addressForm.controls['onlineStoreUrl'].setValue('');
      this.addressForm.controls['onlineStoreUrl'].clearValidators();
      this.addressForm.controls['onlineStoreUrl'].updateValueAndValidity();
    } else {
      this.addressForm.controls['onlineStoreUrl'].setValidators(validators);
      this.addressForm.controls['onlineStoreUrl'].updateValueAndValidity();
    }
  }

  handleOfflineStoreChecked() {
    this.gaService.addEvent('kyc_registration_step_upload_business_type_offline', 'Register_otp', '')
    this.offlineStoreChecked = !this.offlineStoreChecked
    if (!this.offlineStoreChecked) {
      this.addressForm.controls['offlineStorePhotoUrl'].setValue('');
    }
  }

  handleCheck() {
    this.isCheck = !this.isCheck;
    if (this.isCheck) {
      this.shouldNameEqual = false;
    } else if (!this.isCheck) {
      this.shouldNameEqual = true;
    }
  }

  tncModal() {
    const modalRef = this.modalService.open(TncVerificationComponent, {
      title: "Syarat dan Ketentuan Umum Layanan",
      closeButton: true
    });
    modalRef.onResult().subscribe((closed) => {
      this.isCheck = closed;
    });
  }

  // Start handle logic for create data
  createPersonalInformation() {
    const inputData: CreatePersonalInformationInputDto = {
      createPersonalInformationInputV2: {
        createBankAccountInput: {
          name: this.bankForm.controls['name'].value,
          bankId: this.bankForm.controls['bankId'].value,
          accountNumber: this.bankForm.controls['accountNumber'].value
        },
        createPersonalInformationInput: {
          name: this.personalForm.controls['name'].value,
          identityCardImage: this.personalForm.controls['identityCardImage'].value,
          identityCardNumber: this.personalForm.controls['identityCardNumber'].value,
          selfieIdentityCardImage: this.personalForm.controls['selfieIdentityCardImage'].value,
          signatureImage: this.personalForm.controls['signatureImage'].value,
          taxIdentityCardNumber: this.personalForm.controls['taxIdentityCardNumber'].value,
          email: this.personalForm.controls['email'].value,
          referralCode: this.personalForm.controls['referralCode'].value,
        }
      }
    }
    this.verificationService.createPersonalInfoMutation(inputData).subscribe({
      next: (data) => {
        this.gaService.addEvent('kyc_registration_step_personal_information_success', 'Register_kyc', '')
        if (data) {
          this.showStep1 = 'none';
          this.showStep2 = '';
          this.stepCheck = true;
          this.percentage = '100%';
        }
        return data;
      }, error: (error) => {
        const [graphqlError] = error.graphQLErrors;
        const errorMessage = graphqlError?.message ? graphqlError?.message : '';
        this.gaService.addEvent('kyc_registration_step_personal_information_' + errorMessage, 'Register_kyc', '')
        if (
          graphqlError?.message ===
          ERROR_MESSAGE.INVALID_REFERRAL
        ) {
          this.invalidReferral = true;
        }
      }
    })
  }

  createAddressInformation() {
    this.loadingSubmit = true;
    const data: CreateAddressDetailInputDto = {
      createAddressDetailInput: {
        name: this.addressForm.controls['name'].value,
        provinceId: this.addressForm.controls['provinceId'].value,
        regencyId: this.addressForm.controls['regencyId'].value,
        districtId: this.addressForm.controls['districtId'].value,
        villageId: this.addressForm.controls['villageId'].value,
        postalCodeId: this.addressForm.controls['postalCodeId'].value,
        description: this.addressForm.controls['description'].value,
        latitude: this.addressForm.controls['latitude'].value,
        longitude: this.addressForm.controls['longitude'].value,
        onlineStoreUrl: this.addressForm.controls['onlineStoreUrl'].value,
        offlineStorePhotoUrl: this.addressForm.controls['offlineStorePhotoUrl'].value
      }
    };

    this.verificationService.createAddressMutation(data).subscribe({
      next: (dataAddress) => {
        this.loadingSubmit = false;
        if (dataAddress) {
          this.verificationService.createMerchantKycMutation().subscribe({
            next: (data) => {
              const stateData = {
                title: 'Verifikasi sedang diproses...',
                isFromRegister: false
              };
              const navigationExtras: NavigationExtras = {
                state: stateData
              };
              if (data) {
                this.router.navigate(["/verification/success"],
                  navigationExtras);
              }
            }
          })
        }
      }, error: (error) => {
        this.loadingSubmit = false;
        return error;
      }
    })
  }

  // end handle logic for create data


  // start maps logic function
  handleShowMap() {
    this.showMap = !this.showMap;
  }

  // eslint-disable-next-line @typescript-eslint/no-explicit-any
  selectLocation($event: any) {
    this.pickLocationTemp = $event;
  }

  setPickLocation() {
    this.addressForm.controls['postalCodeId'].setValue('');
    this.addressForm.controls['provinceId'].setValue(this.pickLocationTemp.village.district?.regency?.province?.id);
    this.addressForm.controls["regencyId"].setValue(this.pickLocationTemp.village.district?.regency?.id);
    this.addressForm.controls["districtId"].setValue(this.pickLocationTemp.village.district?.id);
    this.addressForm.controls["villageId"].setValue(this.pickLocationTemp.village?.id);
    this.addressForm.controls["latitude"].setValue(this.pickLocationTemp.latitude);
    this.addressForm.controls["longitude"].setValue(this.pickLocationTemp.longitude);
    this.addressMap = `${this.pickLocationTemp.addressDetail.substring(0, 50)}...`;
    this.showMap = !this.showMap;
  }

  // end maps logic function

  goToLoginKyc() {
    this.location.historyGo(-3)
    // this.router.navigate(['/register/login-kyc'])
  }
}
