import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { BasicContainerComponent } from '../../shared/layout/container/basic-container/basic-container.component';
import { PickLocationComponent } from './pick-location/pick-location.component';
import { SetLocationComponent } from './set-location/set-location.component';

const routes: Routes = [
  {
    path: '',
    component: BasicContainerComponent,
    children: [
      {
        path: 'set',
        component: SetLocationComponent,
        title: 'Atur-Lokasi',
        data: {
          pageTitle: "Atur Lokasi",
        }
      },
      {
        path: 'pick',
        component: PickLocationComponent,
        title: 'Setel Lokasi'
      }
    ]
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class LocationRoutingModule {}
