/* eslint-disable @typescript-eslint/no-explicit-any */
import {Location} from "@angular/common";
import {Component, OnInit} from "@angular/core";
import {Router} from "@angular/router";
import {ModalService} from "../../../shared/service/modal/modal.service";
import {ModalSuccessLocationComponent} from "../modal-success-location/modal-success-location.component";
import {FormBuilder, FormGroup, Validators} from "@angular/forms";
import {AuthData} from "../../../shared/data/auth.data";
import {WindowActionService} from "../../../shared/service/window-action.service";
import {
  Address,
  DetailVillageDto,
  getDetailLocationByVillageIdDto,
  LocationApiService,
  VillageService
} from "@youtap-web-lib";

@Component({
  selector: "set-location",
  templateUrl: "./set-location.component.html"
})
export class SetLocationComponent implements OnInit {
  locationForm!: FormGroup;
  loading = false;
  pickLocationModal = false;
  pickLocationTemp!: Address;
  addressMap = "";

  constructor(
    private location: Location,
    private router: Router,
    private modalService: ModalService,
    private formBuilder: FormBuilder,
    private villageService: VillageService,
    private authData: AuthData,
    private windowActionService: WindowActionService,
    private locationApiService: LocationApiService,
  ) {
    this.locationForm = this.formBuilder.group({
      provinceId: ["", Validators.compose([Validators.required])],
      regencyId: ["", Validators.compose([Validators.required])],
      districtId: ["", Validators.compose([Validators.required])],
      villageId: ["", Validators.compose([Validators.required])]
    });
  }

  ngOnInit() {
    this.checkLocationReady();
  }

  checkLocationReady() {
    const detail = this.authData.location;
    if (Object.keys(detail).length) {
      this.pickLocationTemp = detail;
      this.locationForm.controls["provinceId"].setValue(detail.province.id);
      this.locationForm.controls["regencyId"].setValue(detail.regency?.id);
      this.locationForm.controls["districtId"].setValue(detail.district?.id);
      this.locationForm.controls["villageId"].setValue(detail.village?.id);
    }
  }

  save() {
    this.loading = true;
    const modalRef = this.modalService.open(ModalSuccessLocationComponent, {
      title: "",
      closeButton: true,
      data: {
        content: "Lokasi berhasil dipilih.."
      }
    });
    const data: DetailVillageDto = {
      detailVillageInput: {
        villageId: this.locationForm.controls["villageId"].value
      }
    };
    this.villageService.detailVillage(data).subscribe({
      next: ({data}) => {
        if (data) {
          this.setDetailLocationByVillageId();
        }

        setTimeout(() => {
          modalRef.close("");
          this.location.back();
          setTimeout(() => {
            this.windowActionService.reload()
          }, 1000)
        }, 2000);
      },
      error: () => {
        this.loading = false;
        modalRef.close("");
      }
    });
  }

  selectLocation($event: any) {
    this.pickLocationTemp = $event;

  }

  setPickLocation() {
    this.locationForm.controls["provinceId"].setValue(this.pickLocationTemp.village.district?.regency?.province?.id);
    this.locationForm.controls["regencyId"].setValue(this.pickLocationTemp.village.district?.regency?.id);
    this.locationForm.controls["districtId"].setValue(this.pickLocationTemp.village.district?.id);
    this.locationForm.controls["villageId"].setValue(this.pickLocationTemp.village?.id);
    this.addressMap = `${this.pickLocationTemp.addressDetail.substring(0, 50)}...`;
    this.pickLocationModal = !this.pickLocationModal;
  }

  setDetailLocationByVillageId() {
    const data: getDetailLocationByVillageIdDto = {
      detailLocationByVillageIdInput: {
        villageId: this.locationForm.controls["villageId"].value,
      }
    }
    this.locationApiService.detailLocationByVillageId(data).subscribe({
      next: (data) => {
        const detailVillage = data.data.detailLocationByVillageId.village;
        const address: Address = {
          id: "",
          name: "",
          postalCode: "",
          addressDetail: this.pickLocationTemp ? this.pickLocationTemp.addressDetail : "",
          recipientName: "",
          recipientPhone: "",
          recipientEmail: "",
          isDefault: false,
          latitude: data.data.detailLocationByVillageId.latitude,
          longitude: data.data.detailLocationByVillageId.longitude,
          province: {
            id: detailVillage.district?.regency?.province?.id || "",
            name: detailVillage.district?.regency?.province?.name || ""
          },
          regency: {
            id: detailVillage.district?.regency?.id || "",
            name: detailVillage.district?.regency?.name || ""
          },
          district: {
            id: detailVillage.district?.id || "",
            name: detailVillage.district?.name || ""
          },
          village: {
            id: detailVillage?.id || "",
            name: detailVillage?.name || ""
          }
        };

        this.authData.location = address;
      }
    })
  }

}
