import {
  Component,
  EventEmitter,
  forwardRef,
  Input,
  Output
} from '@angular/core';
import {District, DistrictService, ListDistrictDto} from '@youtap-web-lib';
import {data} from 'autoprefixer';
import {NG_VALUE_ACCESSOR} from '@angular/forms';

@Component({
  selector: 'district-dropdown',
  templateUrl: './district-dropdown.component.html',
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: forwardRef(() => DistrictDropdownComponent),
      multi: true
    }
  ]
})
export class DistrictDropdownComponent {
  dataDistrict: District[] = [];
  value: District = {id: '', name: ''};
  id = '';
  isBusy = false;
  isReady = false;
  @Input() disabled = false;
  @Input() placeHolder = 'Pilih Kecamatan';
  @Output() valueChange: EventEmitter<string> = new EventEmitter<string>();

  @Input() set regencyIds(data: string[]) {
    if (data.length) {
      this.search = '';
      this.value = {id: '', name: ''}
      this.dataDistrict = [];
      this.regencyId = data;
      this.getListDistrict(data);
    }
  }
  shareModal = false;
  search = '';
  regencyId!: string[];

  constructor(private districtService: DistrictService) {
  }

  ngAfterViewInit() {
    Promise.resolve().then(() => (this.isReady = true));
  }

  onChange: (data: string) => void = () => {
    data;
  };

  onTouch: (data: string) => void = () => {
    data;
  };

  changeDrop(data: District) {
    this.onChange(data.id);
    this.onTouch(data.id);
    this.value = data;
    this.valueChange.emit(data.id);
    this.shareModal = false;
  }

  // this method sets the value programmatically
  writeValue(value: string) {
    if (value) {
      if(this.dataDistrict.length){
        this.dataDistrict.map((val) => {
          if (val.id === value) {
            this.value = val;
            this.id = value
          }else {
            this.id = value;
          }
        })
      }
      this.id = value;
    } else {
      this.id = "";
      this.value = {id: '', name: ''};
    }
  }

  // upon UI element value changes, this method gets triggered
  registerOnChange(fn: (data: string) => void) {
    this.onChange = fn;
  }

  // upon touching the element, this method gets triggered
  registerOnTouched(fn: (data: string) => void) {
    this.onTouch = fn;
  }

  getListDistrict(id: string[]) {
    this.isBusy = true;
    const data: ListDistrictDto = {
      listDistrictInput: {
        limit: 50,
        page: 0,
        search: {
          keyword: this.search
        },
        filter: {
          regencyIds: id
        }
      }
    };
    this.districtService.listDistrict(data).subscribe({
      next: ({data}) => {
        this.dataDistrict = data.listDistrict.districts;
        if (this.dataDistrict) {
          data.listDistrict.districts.map((val) => {
            if (val.id === this.id) {
              this.value = val;
            }
          });
        }
        this.isBusy = false;
      },
      error: (err) => {
        err;
        this.isBusy = false;
      }
    });
  }

  openShareModal() {
    if (!this.shareModal) {
      this.shareModal = true;
      this.search = '';
      this.dataDistrict = [];
      this.getListDistrict(this.regencyId);
    }
  }

  searchDistrict(){
    setTimeout(() => {
      this.dataDistrict = [];
      this.getListDistrict(this.regencyId);
    }, 1500);
  }
}
