import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { Subject, Observable, Observer } from 'rxjs';
import { WebcamImage, WebcamInitError, WebcamUtil } from 'ngx-webcam';
import { environment } from 'apps/youtap-bos/src/environments/environment';
import {UploadFileService} from "../../service/upload-file/upload-file.service";
import {UploadSingleMediaGCSDto} from "../../service/upload-file/upload-file.dto";
import {MediaFolderTypes} from "@youtap-web-lib";
import {Modal} from "../../service/modal/modal.model";
import {InputModalDto} from "../../service/modal/modal.service";

@Component({
  selector: 'lib-upload-selfie-image',
  templateUrl: './upload-image-selfie.component.html'
})
export class UploadImageSelfieComponent extends Modal implements OnInit {
  // toggle webcam on/off
  @Input() showWebcam = false;
  @Input() confirm = false;
  @Output() selfieIdentityCardImage = new EventEmitter<string>();
  file!: File;
  @Output() handleClick = new EventEmitter<string>();
  @Output() signaturePage = new EventEmitter<boolean>();

  constructor(private uploadFileService: UploadFileService) {
    super();
  }
  public allowCameraSwitch = true;
  public multipleWebcamsAvailable = false;
  public deviceId!: string;

  public videoOptions: MediaTrackConstraints = {
    width: { ideal: 1024 },
    height: { ideal: 520 }
  };
  public errors: WebcamInitError[] = [];

  // latest snapshot
  public webcamImage: WebcamImage | null = null;

  // webcam snapshot trigger
  private trigger: Subject<void> = new Subject<void>();
  // switch to next / previous / specific webcam; true/false: forward/backwards, string: deviceId
  private nextWebcam: Subject<boolean | string> = new Subject<
    boolean | string
  >();
  imageSrc: string | ArrayBuffer | null =
    './assets/images/png/iphone-selfie.png';

  public ngOnInit(): void {
    WebcamUtil.getAvailableVideoInputs().then(
      (mediaDevices: MediaDeviceInfo[]) => {
        this.multipleWebcamsAvailable = mediaDevices && mediaDevices.length > 1;
      }
    );
  }

  public triggerSnapshot(): void {
    this.trigger.next();
    this.confirm = true;
    this.showWebcam = false;
  }

  public toggleWebcam(): void {
    this.showWebcam = !this.showWebcam;
  }

  public handleInitError(error: WebcamInitError): void {
    this.errors.push(error);
  }

  public showNextWebcam(directionOrDeviceId: boolean | string): void {
    this.nextWebcam.next(directionOrDeviceId);
  }

  public repeat(): void {
    this.showWebcam = false;
    this.confirm = false;
    this.imageSrc = './assets/images/png/iphone-selfie.png';
  }

  public handleImage(webcamImage: WebcamImage): void {
    this.imageSrc = webcamImage.imageAsDataUrl;

    this.dataURItoBlob(webcamImage.imageAsBase64).subscribe((blob: Blob) => {
      const imageBlob: Blob = blob;
      const imageName: string = this.generateName();
      this.file = new File([imageBlob], imageName, {
        type: 'image/jpeg'
      });
    });
  }

  /**Method to Generate a Name for the Image */
  generateName(): string {
    const date: number = new Date().valueOf();
    let text = '';
    const possibleText =
      'ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789';
    for (let i = 0; i < 5; i++) {
      text += possibleText.charAt(
        Math.floor(Math.random() * possibleText.length)
      );
    }
    // Replace extension according to your media type like this
    return date + '.' + text + '.jpeg';
  }

  /* Method to convert Base64Data Url as Image Blob */
  dataURItoBlob(dataURI: string): Observable<Blob> {
    return Observable.create((observer: Observer<Blob>) => {
      const byteString: string = window.atob(dataURI);
      const arrayBuffer: ArrayBuffer = new ArrayBuffer(byteString.length);
      const int8Array: Uint8Array = new Uint8Array(arrayBuffer);
      for (let i = 0; i < byteString.length; i++) {
        int8Array[i] = byteString.charCodeAt(i);
      }
      const blob = new Blob([int8Array], { type: 'image/jpeg' });
      observer.next(blob);
      observer.complete();
    });
  }

  public cameraWasSwitched(deviceId: string): void {
    this.deviceId = deviceId;
  }

  public get triggerObservable(): Observable<void> {
    return this.trigger.asObservable();
  }

  public get nextWebcamObservable(): Observable<boolean | string> {
    return this.nextWebcam.asObservable();
  }

  onFileInput(event: Event) {
    this.confirm = true;
    this.showWebcam = false;
    // Handle the file input event here
    const target = event.target as HTMLInputElement;
    const file: File = (target.files as FileList)[0];
    this.file = file;
    if (file) {
      const reader = new FileReader();
      reader.onload = () => (this.imageSrc = reader.result);

      reader.readAsDataURL(file);
    }
  }

  onSubmitImage() {
    const data: UploadSingleMediaGCSDto = {
      createSingleMediaInput: {
        file: this.file,
        folder: MediaFolderTypes.SELFIE_WITH_ID
      }
    };

    this.uploadFileService
      .uploadSingleMediaRegistrationGCS(environment.BOS_API_GRAPHQL_URL, data)
      // eslint-disable-next-line @typescript-eslint/no-explicit-any
      .then(({ data }: any) => {
        this.selfieIdentityCardImage.emit(data.uploadSingleMediaRegistrationGCS.url);
        this.signaturePage.emit(true);
        this.close(data.uploadSingleMediaRegistrationGCS.url)
      })
      .catch((err) => {
        console.error(err);
      });
  }

  onInjectInputs(inputs: InputModalDto): void {
    this.imageSrc = inputs.data.imageSrc
  }

  override dismiss(output?: any) {
    super.dismiss(output);
  }
}
