import { HttpClient } from '@angular/common/http';
import {
  AfterViewInit,
  Component,
  ElementRef,
  EventEmitter,
  Input,
  NgZone,
  Output,
  ViewChild
} from '@angular/core';
import { GoogleMap } from '@angular/google-maps';
import { LocationService } from '../../../service/location.service';
import {
  DetailLocation,
  getDetailLocationDto, Location,
  LocationApiService,
  RequestListSuggestionLocationDto
} from "@youtap-web-lib";

@Component({
  selector: 'lib-google-maps',
  templateUrl: './google-maps.component.html',
  styleUrls: ['./google-maps.component.scss']
})
export class GoogleMapsComponent implements AfterViewInit {
  public searchElementRef!: ElementRef;
  @ViewChild(GoogleMap)
  public map!: GoogleMap;
  listLocation!: Location[];
  locationSelect!: any;
  detailLocation!: DetailLocation;
  mapCenter!: google.maps.LatLngLiteral;
  @Input() apiKey = '';
  @Input() width = '100%';
  @Input() height = '500px';
  @Input() zoom = 19;
  @Input() position!: google.maps.LatLngLiteral;
  options: google.maps.MapOptions = {
    zoomControl: true,
    scrollwheel: false,
    disableDefaultUI: true,
    fullscreenControl: false,
    disableDoubleClickZoom: true
  };
  markerOptions: google.maps.MarkerOptions = { draggable: false };
  // eslint-disable-next-line  @typescript-eslint/no-explicit-any
  @Output() dragMarker: EventEmitter<any> = new EventEmitter();
  // eslint-disable-next-line  @typescript-eslint/no-explicit-any
  @Output() positionValue: EventEmitter<any> = new EventEmitter();
  @Output() selectedPosition: EventEmitter<DetailLocation> = new EventEmitter();
  showMap = true;

  constructor(
    private httpClient: HttpClient,
    private ngZone: NgZone,
    private locationApiService: LocationApiService,
    private locationService: LocationService
  ) {}

  ngAfterViewInit() {
    if (!this.position?.lng) {
      this.locationService
        .getPosition()
        .then((val) => {
          this.position = {
            lat: val.lat,
            lng: val.lng
          };
          this.mapCenter = this.position;
          this.onSelectLocation('', this.position.lat, this.position.lng);
        })
        .catch(() => {
          this.position = {
            lat: -6.1823119,
            lng: 106.8014452
          };
          this.mapCenter = this.position;
          this.onSelectLocation('', this.position.lat, this.position.lng);
        });
    } else {
      this.mapCenter = this.position;
    }
  }

  getListSuggestion(event: any) {
    const search = event?.query;
    const data: RequestListSuggestionLocationDto = {
      listSuggestionLocationInput: {
        keyword: search
      }
    };
    this.locationApiService.lisSuggestionLocation(data).subscribe({
      next: ({ data }) => {
        this.listLocation = data.listSuggestionLocation.locations;
      }
    });
  }

  onSelectLocation(id: string, lat: number, lng: number) {
    const detail: getDetailLocationDto = {
      detailLocationInput: {
        ...(id && { id: id }),
        ...(lat && { latitude: lat }),
        ...(lng && { longitude: lng })
      }
    };
    this.locationApiService.detailLocation(detail).subscribe({
      next: ({ data }) => {
        this.detailLocation = data.detailLocation;
        this.position = {
          lat: lat ? lat : data.detailLocation.latitude,
          lng: lng ? lng : data.detailLocation.longitude
        };
        this.mapCenter = {
          lat: lat ? lat : data.detailLocation.latitude,
          lng: lng ? lng : data.detailLocation.longitude
        };

        this.selectedPosition.emit(data.detailLocation);
      }
    });
  }

  mapDragTest() {
    if (this.map) {
      this.position = {
        lat: this.map.getCenter()?.lat() || 0,
        lng: this.map.getCenter()?.lng() || 0
      };
    }
  }

  mapDragend() {
    this.onSelectLocation('', this.position.lat, this.position.lng);
  }
}
