import {
  Component,
  EventEmitter,
  forwardRef,
  Input,
  OnInit,
  Output
} from '@angular/core';
import { ListProvinceDto, Province, ProvinceService } from '@youtap-web-lib';
import { NG_VALUE_ACCESSOR } from '@angular/forms';
import { data } from 'autoprefixer';

@Component({
  selector: 'province-dropdown',
  templateUrl: './province-dropdown.component.html',
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: forwardRef(() => ProvinceDropdownComponent),
      multi: true
    }
  ]
})
export class ProvinceDropdownComponent implements OnInit {
  value: Province = { id: '', name: '' };
  id = '';
  dataProvince: Province[] = [];
  @Input() placeHolder = 'Pilih Provinsi';
  @Output() valueChange: EventEmitter<string> = new EventEmitter<string>();
  isLoading = true;
  shareModal = false;
  search = '';

  constructor(private provinceService: ProvinceService) {}

  ngOnInit(): void {
    this.getListProvince();
  }

  openShareModal() {
    if (!this.shareModal) {
      this.shareModal = true;
      this.search = '';
      this.dataProvince = [];
      this.getListProvince();
    }
  }

  onChange: (data: string) => void = () => {
    data;
  };
  onTouch: (data: string) => void = () => {
    data;
  };

  changeDrop(data: Province) {
    this.onChange(data.id);
    this.onTouch(data.id);
    this.value = data;
    this.valueChange.emit(data.id);
    this.shareModal = false;
  }

  // this method sets the value programmatically
  writeValue(value: string) {
    if (value && this.dataProvince.length) {
      this.dataProvince.map((val) => {
        if (val.id === value) {
          this.value = val;
          this.id = value;
        }
      });
    } else {
      this.value = { id: '', name: '' };
      this.id = value;
    }
  }

  // upon UI element value changes, this method gets triggered
  registerOnChange(fn: (data: string) => void) {
    this.onChange = fn;
  }

  // upon touching the element, this method gets triggered
  registerOnTouched(fn: (data: string) => void) {
    this.onTouch = fn;
  }

  getListProvince() {
    this.isLoading = true;
    const data: ListProvinceDto = {
      listProvinceInput: {
        limit: 50,
        page: 0,
        search: this.search,
      }
    };
    this.provinceService.listProvince(data).subscribe({
      next: ({ data }) => {
        this.dataProvince = data.listProvince.provinces;
        if (this.dataProvince) {
          data.listProvince.provinces.map((val) => {
            if (val.id === this.id) {
              this.value = val;
            }
          });
        }
        this.isLoading = false;
      },
      error: (err) => {
        err;
        this.isLoading = false;
      }
    });
  }

  searchProvince(){
    setTimeout(() => {
      this.dataProvince = [];
      this.getListProvince();
    }, 1500);
  }
}
