import {Component} from '@angular/core';
import {Router} from '@angular/router';
import {AppData} from '../../../shared/data/app-data';

@Component({
  selector: 'success-register',
  templateUrl: './success-register.component.html',
  styleUrls: ['./success-register.component.scss']
})
export class SuccessRegisterComponent {
  title = 'Verifikasi lagi diproses....';

  constructor(
    private router: Router,
    public appData: AppData,
  ) {
  }

  goToLogin() {
    localStorage.removeItem('access_token')
    this.router.navigate(['/login'], {queryParams: {stateUrl: "/home"}});
  }
}
