import {
  ChangeDetectorRef,
  Component,
  EventEmitter,
  forwardRef,
  Input,
  Output
} from '@angular/core';
import {NG_VALUE_ACCESSOR} from '@angular/forms';
import {data} from 'autoprefixer';
import {ListRegencyDto, Regency, RegencyService} from "@youtap-web-lib";

@Component({
  selector: 'regency-dropdown',
  templateUrl: './regency-dropdown.component.html',
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: forwardRef(() => RegencyDropdownComponent),
      multi: true
    }
  ]
})
export class RegencyDropdownComponent {
  value: Regency = {id: '', name: ''};
  id = '';
  dataRegency: Regency[] = [];
  isBusy = false;
  isReady = false;
  @Input() disabled = true;
  @Input() placeHolder = 'Pilih Kota/Kabupaten';

  @Input() set provinceIds(data: string[]) {
    if (data.length) {
      this.search = '';
      this.value = {id: '', name: ''};
      this.dataRegency = [];
      this.provinceId = data;
      this.getListRegency(data);
    }
  }

  @Output() valueChange: EventEmitter<string> = new EventEmitter<string>();
  shareModal = false;
  search = '';
  provinceId!: string[];

  constructor(
    private cd: ChangeDetectorRef,
    private regencyService: RegencyService
  ) {
  }

  ngAfterViewInit() {
    Promise.resolve().then(() => (this.isReady = true));
  }

  onChange: (data: string) => void = () => {
    data;
  };
  onTouch: (data: string) => void = () => {
    data;
  };

  changeDrop(data: Regency) {
    this.onChange(data.id);
    this.onTouch(data.id);
    this.value = data;
    this.valueChange.emit(data.id);
    this.shareModal = false;
  }

  // this method sets the value programmatically
  writeValue(value: string) {
    if (value) {
      this.id = value;
    } else {
      this.id = "";
      this.value = {id: '', name: ''};
    }
  }

  // upon UI element value changes, this method gets triggered
  registerOnChange(fn: (data: string) => void) {
    this.onChange = fn;
  }

  // upon touching the element, this method gets triggered
  registerOnTouched(fn: (data: string) => void) {
    this.onTouch = fn;
  }

  getListRegency(id: string[]) {
    this.dataRegency = []
    this.isBusy = true;
    const data: ListRegencyDto = {
      listRegencyInput: {
        limit: 50,
        page: 0,
        search: this.search,
        filter: {
          provinceIds: id
        }
      }
    };
    this.regencyService.listRegency(data).subscribe({
      next: ({data}) => {
        this.dataRegency = data.listRegency.regencies;
        if (this.dataRegency) {
          data.listRegency.regencies.map((val) => {
            if (val.id === this.id) {
              this.value = val;
            }
          });
        }
        this.isBusy = false;
      },
      error: () => {
        this.isBusy = false;
      }
    });
  }

  openShareModal() {
    if (!this.shareModal) {
      this.shareModal = true;
      this.search = '';
      this.dataRegency = [];
      this.getListRegency(this.provinceId);
    }
  }

  searchRegency(){
    setTimeout(() => {
      this.dataRegency = [];
      this.getListRegency(this.provinceId);
    }, 1500);
  }
}
