import {
  AfterViewInit,
  Component,
  ElementRef,
  EventEmitter,
  OnInit,
  Output,
  ViewChild
} from '@angular/core';
import {environment} from 'apps/youtap-bos/src/environments/environment';
import {UploadFileService} from "../../../service/upload-file/upload-file.service";
import {UploadSingleMediaGCSDto} from "../../../service/upload-file/upload-file.dto";
import {MediaFolderTypes} from "@youtap-web-lib";
import {GaService} from "../../../service/ga.service";

interface IPosition {
  x: number;
  y: number;
}

@Component({
  selector: 'lib-signature-input',
  templateUrl: './signature-input.component.html'
})
export class SignatureInputComponent implements AfterViewInit, OnInit {
  // Canvas size
  @ViewChild('signatureWrapper') signatureWrapper!: ElementRef;
  public canvasWidth = 350;
  public canvasHeight = 300;
  public dataUrl = '';
  imgSrc: string | ArrayBuffer | null = '';

  @Output() signatureSaved = new EventEmitter();
  showCanvas = true;
  show = false;
  file!: File;
  @Output() signatureImg = new EventEmitter<string>();

  @Output() handleEmit = new EventEmitter<string>();

  public signatureImgLocal!: string | null | ArrayBuffer | File | Blob;
  // Canvas setup
  @ViewChild('signatureCanvas') signatureCanvas!: ElementRef;
  public context!: CanvasRenderingContext2D;
  private penWidth = 1;

  // Drawing
  public isDrawCanvas = false;
  public drawing = false;
  private currentPosition: IPosition = {x: 0, y: 0};
  private lastPosition: IPosition = this.currentPosition;

  constructor(
    private uploadFileService: UploadFileService,
    private gaService: GaService,) {
  }

  // Init
  ngOnInit(): void {
    if (this.signatureWrapper) {
      this.canvasWidth = this.signatureWrapper.nativeElement.offsetWidth;
      this.canvasHeight = this.signatureWrapper.nativeElement.offsetHeight;
    }
  }

  ngAfterViewInit(): void {
    this.context = this.signatureCanvas.nativeElement.getContext('2d');
    this.context.lineWidth = this.penWidth;
    this.context.lineCap = 'round';
    this.context.lineJoin = 'round';

    window.requestAnimationFrame =
      window.requestAnimationFrame ||
      function (callback: TimerHandler) {
        window.setTimeout(callback, 1000 / 60);
      };

  }

  toggleShowCanvas() {
    this.clearSignature();
    this.showCanvas = true;
  }

  // Renderer
  private renderLoop(): void {
    window.requestAnimationFrame(this.renderLoop );
    this.renderCanvas();
  }

  private renderCanvas(): void {
    if (this.drawing) {
      this.context.moveTo(this.lastPosition.x, this.lastPosition.y);
      this.context.lineTo(this.currentPosition.x, this.currentPosition.y);

      this.context.stroke();
      this.isDrawCanvas = true;

      this.lastPosition = this.currentPosition;
    }
  }

  public downloadSignature(): void {
    const e = document.createElement('a');
    e.setAttribute(
      'href',
      `data:image/png;base64, ${this.signatureCanvas.nativeElement
        .toDataURL('image/png')
        .replace(/^data:image\/(png|jpg);base64,/, '')}`
    );
    e.setAttribute('download', 'YourSignature.png');
    e.style.display = 'none';
    document.body.appendChild(e);
    e.click();
    document.body.removeChild(e);
  }

  public saveSignature(): void {
    this.showCanvas = false;
    const imageUri = this.signatureCanvas.nativeElement.toDataURL('image/png');
    this.signatureSaved.emit(this.signatureImgLocal);
    const blob = this.dataURLToBlob(imageUri);
    const file = new File([blob], 'signature.png', {type: 'image/png'});
    this.signatureImgLocal = file;
  }

  dataURLToBlob(dataURL: string) {
    const parts = dataURL.split(';base64,');
    const contentType = parts[0].split(':')[1];
    const raw = window.atob(parts[1]);
    const rawLength = raw.length;
    const uInt8Array = new Uint8Array(rawLength);

    for (let i = 0; i < rawLength; ++i) {
      uInt8Array[i] = raw.charCodeAt(i);
    }

    return new Blob([uInt8Array], {type: contentType});
  }

  public clearSignature(): void {
    this.gaService.addEvent('kyc_registration_step_retake_signature', 'Register_otp', '')
    this.imgSrc = '';
    this.showCanvas = true;
    this.isDrawCanvas = false;
    this.context.clearRect(
      0,
      0,
      this.signatureCanvas.nativeElement.width,
      this.signatureCanvas.nativeElement.height
    );
    this.signatureCanvas.nativeElement.width =
      // eslint-disable-next-line no-self-assign
      this.signatureCanvas.nativeElement.width;
    this.signatureImgLocal = '';
  }

  // Mouse events
  public mouseStartDraw(e: MouseEvent): void {
    this.drawing = true;
    this.lastPosition = this.getMousePosition(e);
  }

  public mouseStopDraw(): void {
    this.drawing = false;
  }

  public mouseDraw(e: MouseEvent): void {
    this.currentPosition = this.getMousePosition(e);
    this.renderCanvas();
  }

  private getMousePosition(e: MouseEvent): IPosition {
    const rect = this.signatureCanvas.nativeElement.getBoundingClientRect();

    return {
      x: e.clientX - rect.left,
      y: e.clientY - rect.top
    };
  }

  // Touch events
  public touchStartDraw(e: TouchEvent): void {
    this.currentPosition = this.getTouchPosition(e);

    const touch = e.touches[0];
    const mouseEvent = new MouseEvent('mousedown', {
      clientX: touch.clientX,
      clientY: touch.clientY
    });

    this.signatureCanvas.nativeElement.dispatchEvent(mouseEvent);
  }

  public touchStopDraw(): void {
    const mouseEvent = new MouseEvent('mouseup', {});
    this.signatureCanvas.nativeElement.dispatchEvent(mouseEvent);
  }

  public touchDraw(e: TouchEvent): void {
    this.currentPosition = this.getTouchPosition(e);

    const touch = e.touches[0];
    const mouseEvent = new MouseEvent('mousemove', {
      clientX: touch.clientX,
      clientY: touch.clientY
    });

    this.signatureCanvas.nativeElement.dispatchEvent(mouseEvent);
  }

  private getTouchPosition(e: TouchEvent): IPosition {
    const rect = this.signatureCanvas.nativeElement.getBoundingClientRect();

    return {
      x: e.touches[0].clientX - rect.left,
      y: e.touches[0].clientY - rect.top
    };
  }

  public onFileInput(event: Event) {
    // Handle the file input event here
    const target = event.target as HTMLInputElement;
    const file: File = (target.files as FileList)[0];
    this.signatureImgLocal = file;
    this.showCanvas = false;
    this.isDrawCanvas = true;
    if (file) {
      const reader = new FileReader();
      reader.onload = () => (this.imgSrc = reader.result);

      reader.readAsDataURL(file);
    }
  }

  async onSubmitImage() {
    if (this.showCanvas) {
      await this.saveSignature();
    }
    const data: UploadSingleMediaGCSDto = {
      createSingleMediaInput: {
        file: this.signatureImgLocal as File,
        folder: MediaFolderTypes.PROFILE_IMAGE
      }
    };

    await this.uploadFileService
      .uploadSingleMediaRegistrationGCS(environment.BOS_API_GRAPHQL_URL, data)
      // eslint-disable-next-line @typescript-eslint/no-explicit-any
      .then(({data}: any) => {
        this.gaService.addEvent('kyc_registration_step_upload_signature_success', 'Register_otp', '')
        this.signatureImg.emit(data.uploadSingleMediaRegistrationGCS.url);
        this.handleEmit.emit();
      })
      .catch((err) => {
        this.gaService.addEvent('kyc_registration_step_upload_signature_failed', 'Register_otp', '')
        console.error(err);
      });
  }
}
